/* Register animations to allow JavaScript hooks to react on browser auto fill. */
@-webkit-keyframes onAutoFillStart {
  kopano { keepme: 0 /* hack needed to avoid getting removed since empty */ }
}
@keyframes onAutoFillStart {
  kopano { keepme: 0 /* hack needed to avoid getting removed since empty */ }
}
@-webkit-keyframes onAutoFillCancel {
  kopano { keepme: 0 /* hack needed to avoid getting removed since empty */ }
}
@keyframes onAutoFillCancel {
  kopano { keepme: 0 /* hack needed to avoid getting removed since empty */ }
}

#bg-thumb {
  /* NOTE(longsleep): This is the base64 encoded svg, inlined directly to show
     this image as early as possible.
  */
  background-image: url("data:image/svg+xml;base64,");
}

/* fancy background with two components and fade */

#bg {
  background-color: white;
}

#bg > div {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  z-index: 0;
}

#loader {
  /* NOTE(longsleep): White here needed because of the background image */
  color: white;
  text-shadow: #000 0px 0px 1px;
}

/* NOTE(longsleep): This imports the inline image CSS as generated by make. */

#bg-enhanced {
  opacity: 0;
  -webkit-transition: opacity 1s;
  transition: opacity 1s;
}

#bg-enhanced.enhanced {
  background-image: url(../../static/media/loginscreen-bg.cc3ef0e4.jpg);
  opacity: 1;
}

#bg-enhanced.enhanced:after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: url(../../static/media/loginscreen-bg-overlay.6f9a3fb0.svg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

